import Vue from 'vue'
import { Notify } from 'vant'

Vue.use(Notify)

class ValidForm {
    // 验证有效表单
    verify(options) {
        for (let key in options) {
            if (!options[key].reg.test(options[key].value)) {
                // 提示
                Notify({
                    type: 'warning',
                    message: options[key].errMsg
                })
                // 验证失败
                return false;
            }
        }
        // 验证成功
        return true;
    }
}

export const validForm = new ValidForm();