<template>
  <div class="forget">
    <van-nav-bar title="找回密码" left-text="返回" left-arrow @click-left="goBack" />

    <van-form>
      <van-field v-model="userInfo.phone" name="手机号" label="手机号" placeholder="手机号" />
      <van-field v-model="userInfo.password" name="密码" label="密码" placeholder="密码" type="password" />
      <van-field v-model="userInfo.email" name="邮箱" label="邮箱" placeholder="邮箱" />
      <van-field v-model="userInfo.verifyCode" name="邮箱验证码" label="邮箱验证码" placeholder="邮箱验证码">
        <template #button>
          <van-button
            size="small"
            color="#0b34ba"
            :disabled="isSend"
            @click="sendVerifyCode"
          >{{text}}</van-button>
        </template>
      </van-field>

      <div style="margin: 16px;">
        <van-button round block type="info" color="#0b34ba" @click="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import "../assets/less/forget.less";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      userInfo: {
        phone: "",
        password: "",
        email: "",
        verifyCode: "",
      },

      text: "发送验证码",
      isSend: false,
    };
  },

  methods: {
    // 返回
    goBack() {
      this.$router.push({ name: "Login" });
    },

    // 发送验证码
    sendVerifyCode() {
      let options = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机格式不正确",
        },
        password: {
          value: this.userInfo.password,
          reg: /^[\w\d]{6,16}$/,
          errMsg: "密码格式为6-16位数字或字母组合",
        },
        email: {
          value: this.userInfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errMsg: "邮箱格式不正确",
        },
      };

      if (!validForm.verify(options)) {
        return;
      }

      let time = 60;
      this.text = time + "s后重新发送";
      this.isSend = true;
      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          this.isSend = false;
          this.text = "发送验证码";
        } else {
          time--;
          this.text = time + "s后重新发送";
        }
      }, 1000);

      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let data = utils.queryString({
        email: this.userInfo.email,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      }).then((result) => {
        this.$toast.clear();
        
        if (result.data.code == "J001") {
          this.$notify({
            color: "#fffeff",
            background: "#0b34ba",
            message: result.data.msg,
          });
        } else {
          this.$notify({
            type: "warning",
            message: result.data.msg,
          });
        }
      });
    },

    // 提交表单
    submit() {
      let options = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机格式不正确",
        },
        password: {
          value: this.userInfo.password,
          reg: /^[\w\d]{6,16}$/,
          errMsg: "密码格式为6-16位数字或字母组合",
        },
        email: {
          value: this.userInfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errMsg: "邮箱格式不正确",
        },
        verifyCode: {
          value: this.userInfo.verifyCode,
          reg: /^\d{6}$/,
          errMsg: "验证码格式错误",
        },
      };

      if (!validForm.verify(options)) {
        // return;
      }

      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //   参数序列化
      let data = utils.queryString({
        validCode: this.userInfo.verifyCode,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "K001") {
            this.alterPassword();
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //   修改密码
    alterPassword() {
      this.$toast.loading({
        message: "更改密码中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let data = utils.queryString({
        phone: this.userInfo.phone,
        password: this.userInfo.password,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          
          this.$toast.clear();

          if (result.data.code == "L001") {
            this.$toast(result.data.msg);
            this.$router.push({ name: "Login" });
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>